import React, { useState, useEffect, useRef } from "react";
import {
  Center,
  VStack,
  HStack,
  Text,
  Input,
  Divider,
  Pressable,
} from "native-base";
import _ from "lodash";

import SearchIcon from "./svg/SearchIcon";
import CloseIcon from "./svg/CloseIcon";
import Colors from "../constants/Colors";
import { vh, vw } from "../util";

const GEO_API = "https://reversegeocode.shadow.properties";

const ShadowSearch = ({ sz, setAddress, setRefresh }) => {
  const spRef = useRef([]);
  const ppRef = useRef([]);
  const sRef = useRef();
  const cRef = useRef();
  let aRef = useRef();
  const [predictions, setPredictions] = useState([]);
  const [search, setSearch] = useState("");
  const [placeId, setPlaceId] = useState("");

  // handle click outside
  useEffect(() => {
    function handleClickOutside(event) {
      if (sRef.current && !sRef.current.contains(event.target)) {
        if (global.predictions) {
          setPredictions([]);
          setSearch("");
          aRef.current.value = "";
          delete global.predictions;
        }
      }
    }

    // Bind the event listener
    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      // Unbind the event listener on clean up
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [sRef]);

  useEffect(() => {
    let unmounted = false;
    const getLocations = _.debounce(async (loc) => {
      const url = `${GEO_API}/autocomplete`;
      try {
        const result = await fetch(url, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Token: "AW9054ty6erfgk321",
          },
          body: JSON.stringify({ input: loc }),
        });
        const json = await result.json();
        global.predictions = json.data;
        setPredictions(json.data);
      } catch (err) {
        console.error(err.message());
      }
    }, 500);
    if (!unmounted) {
      if (search !== "") {
        getLocations(search);
      } else {
        setPredictions([]);
      }
    }
    return () => {
      unmounted = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [search]);

  useEffect(() => {
    let unmounted = false;
    const getAddress = _.debounce(async (id) => {
      const url = `${GEO_API}/address`;
      try {
        const result = await fetch(url, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Token: "AW9054ty6erfgk321",
          },
          body: JSON.stringify({ id }),
        });
        const json = await result.json();
        setRefresh((r) => !r);
        setAddress(json.data);
        aRef.current.value = json.data.ff;
      } catch (err) {
        console.error(err.message());
      }
    }, 500);
    if (!unmounted) {
      if (placeId !== "") {
        getAddress(placeId);
      } else {
        setAddress(null);
      }
    }
    return () => {
      unmounted = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [placeId]);

  const searchStyle =
    sz === "md" || sz === "lg" || sz === "xl"
      ? {
          marginTop: vh(30),
          width: "90%",
          marginLeft: vw(40),
          height: vh(48),
          marginRight: vw(20),
          borderWidth: 0,
          boxShadow: "0px 3px 6px #0000001A",
          borderBottomLeftRadius: vh(
            predictions && predictions.length > 0 ? 0 : 50
          ),
          borderBottomRightRadius: vh(
            predictions && predictions.length > 0 ? 0 : 50
          ),
          zIndex: 99999,
        }
      : {
          marginTop: vh(20),
          width: "99%",
          // marginLeft: "5%",
          height: vh(48),
          boxShadow: "0px 3px 6px #0000001A",
          borderBottomLeftRadius: vh(
            predictions && predictions.length > 0 ? 0 : 50
          ),
          borderBottomRightRadius: vh(
            predictions && predictions.length > 0 ? 0 : 50
          ),
          alignSelf: "center",
          zIndex: 99999,
        };
  return (
    <VStack
      ref={sRef}
      bg='white'
      borderRadius={predictions && predictions.length > 0 ? 10 : 50}
      style={searchStyle}
      // padding={vh(5)}
    >
      <HStack
        style={{
          // boxShadow: "0px 3px 6px #0000001A",
          borderRadius: predictions && predictions.length > 0 ? 10 : 50,
          bg: "transparent",
          width: "100%",
        }}
        marginTop={vh(2)}
      >
        <Input
          leftElement={
            <Center bg='white' marginLeft={vw(16)}>
              <SearchIcon />
            </Center>
          }
          name='searchlocation'
          id='searchlocation'
          ref={aRef}
          autoComplete='off'
          bg={"transparent"}
          width='full'
          style={{
            // width: "100%",
            height: vh(45),
            fontSize: vw(15),
            lineHeight: vh(22),
            overflow: "hidden",
            fontFamily: "Poppins_medium",
            color: "#989898",
            borderWidth: 0,
            borderColor: "white",
            backgroundColor: "white",
          }}
          onKeyPress={(e) => {
            if (
              e.key === "ArrowDown" &&
              spRef.current &&
              aRef.current.value !== ""
            ) {
              if (!global.sp) {
                global.sp = 0;
              } else if (global.sp === 5) {
                spRef.current[global.sp - 1].style.color = "#303030";
                spRef.current[global.sp - 1].style.fontWeight = "norml";
                global.sp = 0;
              }
              if (global.sp > 0) {
                spRef.current[global.sp - 1].style.color = "#303030";
                spRef.current[global.sp - 1].style.fontWeight = "normal";
              }
              spRef.current[global.sp].style.color = Colors.primary;
              spRef.current[global.sp].style.fontWeight = "bold";
              global.sp++;
            } else if (
              e.key === "ArrowUp" &&
              spRef.current &&
              aRef.current.value !== ""
            ) {
              if (!global.sp) {
                global.sp = 0;
              }

              if (global.sp > 2) {
                spRef.current[global.sp - 1].style.color = "#303030";
                spRef.current[global.sp - 1].style.fontWeight = "normal";
                spRef.current[global.sp - 2].style.color = Colors.primary;
                spRef.current[global.sp - 2].style.fontWeight = "bold";
                global.sp--;
              } else if (global.sp === 1) {
                global.sp = 5;
                spRef.current[0].style.color = "#303030";
                spRef.current[0].style.fontWeight = "normal";
                spRef.current[global.sp - 1].style.color = Colors.primary;
                spRef.current[global.sp - 1].style.fontWeight = "bold";
              } else {
                spRef.current[global.sp - 1].style.color = "#303030";
                spRef.current[global.sp - 1].style.fontWeight = "normal";
                spRef.current[global.sp - 2].style.color = Colors.primary;
                spRef.current[global.sp - 2].style.fontWeight = "bold";
                global.sp--;
              }
            } else if (e.key === "Enter" && global.sp) {
              // console.log("Pressed", global.sp);
              ppRef.current[global.sp - 1].click();
            } else if (e.key === "Tab") {
              e.preventDefault();
            }
          }}
          _focus={{
            // backgroundColor: "white",
            borderColor: "white",
          }}
          _hover={{
            // backgroundColor: "white",
            borderColor: "white",
          }}
          onChangeText={(v) => {
            cRef.current.style.visibility = v !== "" ? "visible" : "hidden";
            setSearch(v);
          }}
          variant='unstyled'
          // rounded={"full"}
          // roundedTop={"full"}
          placeholder='Search address'
          rightElement={
            <Pressable
              ref={cRef}
              onPress={() => {
                if (search !== "") {
                  setPredictions([]);
                }
                aRef.current.value = "";
                cRef.current.style.visibility = "hidden";
              }}
              style={{
                marginRight: vw(20),
                visibility: "hidden",
                marginTop: vh(5),
              }}
            >
              <CloseIcon />
            </Pressable>
          }
        />
      </HStack>
      {predictions && predictions.length > 0 && (
        <VStack
          space={4}
          style={{
            zIndex: 9999,
            width: "100%",
            paddingTop: vh(5),
            paddingBottom: vh(20),
            paddingHorizontal: vw(20),
            backgroundColor: "white",
            borderBottomLeftRadius: vh(10),
            borderBottomRightRadius: vh(10),
            boxShadow: "0px 3px 6px #0000001A",
          }}
        >
          <Divider
            style={{
              alignSelf: "center",
              backgroundColor: "rgba(0,0,0,0.15)",
              height: vh(1.5),
              width: sz === "sm" || sz === "base" ? "100%" : "100%",
            }}
          />
          {predictions &&
            predictions.length > 0 &&
            predictions.map((p, idx) => {
              // const nit = p.address.toLowerCase().split(search);
              // console.log("Prediction",nit[1].length)
              // console.log(p.address.slice(0,nit[1].length))
              // console.log(splitAt(search.length - 1, p.address));
              return (
                <Pressable
                  onHoverIn={() => {
                    spRef.current[idx].style.color = Colors.primary;
                    spRef.current[idx].style.fontWeight = "bold";
                  }}
                  onHoverOut={() => {
                    spRef.current[idx].style.color = "#303030";
                    spRef.current[idx].style.fontWeight = "normal";
                  }}
                  ref={(el) => (ppRef.current[idx] = el)}
                  key={"pr-" + p.id}
                  onPress={async () => {
                    setPlaceId(p.id);
                    setPredictions([]);
                  }}
                >
                  <Text
                    style={{
                      fontSize: vw(13),
                      fontFamily: "Poppins",
                      color: "rgba(0,0,0,0.4)",
                    }}
                  >
                    {/* {search} */}
                    <Text
                      ref={(el) => (spRef.current[idx] = el)}
                      style={{ color: "#303030" }}
                    >
                      {p.address}
                    </Text>
                  </Text>
                </Pressable>
              );
            })}
        </VStack>
      )}
    </VStack>
  );
};

export default React.memo(ShadowSearch);
