import React, { useRef, useState } from "react";
import {
  FormControl,
  Box,
  Pressable,
  Center,
  Spinner,
  Image,
  VStack,
} from "native-base";
import { CircularProgressbar as ProgressCircle } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";

import Util from "../util";
import { CDN, getUser, API_URL } from "../constants/Config";
import Colors from "../constants/Colors";
import UploadIcon from "./svg/UploadIcon";
const { vw, vh } = Util;

const ShadowUpload = (props) => {
  const uRef = useRef();
  const [progress, setProgress] = useState(
    props.path && props.path !== "0" ? 100 : 0
  );
  const [imgId, setImgId] = useState(props.path || "0");
  const [isLoading, setIsLoading] = useState(false);
  const [imageLoading, setImageLoading] = useState(false);

  const onChangeHandler = async () => {
    const { token, _id } = getUser();
    // var file = uploadFile.current.files[0];
    setProgress(0);
    setIsLoading(true);
    for (const file of uRef.current.files) {
      // Allowed types
      var mime_types = ["image/jpeg", "image/jpg", "image/png"];

      // Validate MIME type
      if (mime_types.indexOf(file.type) === -1) {
        alert("Error : Incorrect file type");
        return;
      }

      // Max 2 Mb allowed
      if (file.size > 2000 * 1024 * 1024) {
        alert("Error : Exceeded size 2GB");
        return;
      }

      var data = new FormData();

      var request = new XMLHttpRequest();

      data.append("picture", file);
      data.append("_id", _id);
      data.append("default", true);

      // AJAX request finished
      request.addEventListener("load", function (e) {
        setIsLoading(false);
      });

      // Upload progress on request.upload
      request.upload.addEventListener("progress", function (e) {
        var completed = (e.loaded / e.total) * 100;
        setProgress(completed);
        setImageLoading(true);
      });

      request.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 200) {
          const result = JSON.parse(this.responseText);
          if (result.success) {
            setImgId(result.data.id);
            props.setPath(result.data.id);
          }
        } else {
          if (this.status > 200) {
            console.log("ON-ERROR", this.readyState, this.status);
            console.log(this.responseText);
            setIsLoading(false);
          }
          // throw new Error("something went wrong");
        }
      };
      // If server is sending a JSON response then set JSON response type
      request.responseType = "text";

      // Send POST request to the server side script
      request.open("POST", `${API_URL}/signatures/upload/images`, true);
      request.setRequestHeader("Authorization", "Bearer " + token);
      request.send(data);
    }
    uRef.current.value = "";
  };
  return (
    <Box>
      <FormControl mb={4}>
        <FormControl.Label
          _text={{ color: props.error ? "#d93025" : "#4C7AD6" }}
          my={2}
        >
          {props.label + " "}
        </FormControl.Label>
        <VStack flex={1}>
          <input
            ref={uRef}
            accept='image/jpg,image/jpeg,image/png'
            name='imgupload'
            type='file'
            onChange={onChangeHandler}
            style={{ display: "none" }}
          />
          <Center style={{ width: vw(64) }}>
            {progress === 0 && imgId === "0" && (
              <Pressable
                onPress={() => {
                  setImgId("0");
                  setProgress(0);
                  uRef.current.click();
                }}
              >
                <UploadIcon
                  width={vw(64)}
                  height={vh(48)}
                  fill={Colors.primary}
                />
              </Pressable>
            )}
            {progress === 100 && imgId !== "0" && (
              <Pressable
                onPress={() => {
                  uRef.current.click();
                }}
              >
                {isLoading ? (
                  <Center width={vw(80)} height={vh(80)}>
                    <Spinner size={"sm"} color={Colors.primary} />
                  </Center>
                ) : (
                  <>
                    <Center
                      style={{ display: imageLoading ? "flex" : "none" }}
                      width={vw(64)}
                      height={vh(64)}
                    >
                      <Spinner size={"sm"} color={Colors.primary} />
                    </Center>
                    <Image
                      alt='Pic'
                      style={{
                        marginLeft: vw(15),
                        display: imageLoading ? "none" : "flex",
                      }}
                      onLoadEnd={() => {
                        console.log("IMAGE-ON-END");
                        setImageLoading(false);
                      }}
                      width={vw(64)}
                      height={vh(64)}
                      source={{
                        uri: `${CDN}/signatures/${imgId}`,
                      }}
                    />
                  </>
                )}
              </Pressable>
            )}
            {progress > 0 && progress < 100 && (
              <ProgressCircle value={progress} text={`${progress}%`} />
            )}
          </Center>
        </VStack>
      </FormControl>
    </Box>
  );
};

export default ShadowUpload;
