import React, { useRef } from "react";
import { FormControl } from "native-base";
import { Controller } from "react-hook-form";
import PhoneInput from "react-phone-input-2";
import {
  isPossiblePhoneNumber,
  isValidPhoneNumber,
  validatePhoneNumberLength,
} from "libphonenumber-js";

import Util from "../util";

const { vw, vh } = Util;

const ShadowPhone = React.forwardRef((props, ref) => {
  const intl = useRef();
  return (
    <FormControl
      isRequired={props.isRequired}
      isInvalid={props.invalid}
      isReadOnly={props.isReadOnly}
      style={{
        height: vh(48),
        ...props.style,
      }}
    >
      {props.label && (
        <FormControl.Label
          style={{
            fontSize: vw(13),
            fontFamily: "Poppins_medium",
            color: props.invalid ? "red.500" : "rgba(102, 102, 102, 0.7)",
            height: vh(19),
          }}
        >
          {props.label}
        </FormControl.Label>
      )}

      <Controller
        style={{
          zIndex: 99999,
        }}
        control={props.control}
        rules={{
          ...props.rules,
          validate: (value) => {
            return (
              isValidPhoneNumber(value, "US") || "Enter a valid phone number"
            );
          },
          // pattern: {
          //   value:
          //     // /^((\+1|1)?( |-)?)?(\([2-9][0-9]{2}\)|[2-9][0-9]{2})( |-)?([2-9][0-9]{2}( |-)?[0-9]{4})$/,
          //     /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{6}$/im,
          //   message: "Enter a valid phone number",
          // },
        }}
        name={props.name}
        render={({ field: { onChange, onBlur, value } }) => (
          <PhoneInput
            ref={intl}
            inputProps={{
              ref,
              name:"tel",
              disabled: props.isReadOnly,
              // onKeyPress:props.onKeyPress
            }}
            country={"us"}
            value={value}
            onChange={onChange}
            onKeyDown={props.onKeyPress}
            containerStyle={{
              zIndex: 999999,
            }}
            dropdownStyle={{
              zIndex: 999999,
              position: "absolute",
              height: vh(100),
            }}
            inputStyle={{
              fontSize: vw(15),
              fontFamily: "Poppins_medium",
              backgroundColor: "white",
              height: vh(37.8),
              width: "100%",
              marginTop: vh(1),
            }}
          />
        )}
      />
      <FormControl.ErrorMessage style={{ marginTop: vh(-3) }}>
        {props.error}
      </FormControl.ErrorMessage>
      <FormControl.HelperText style={{ marginTop: vh(-3) }}>
        {props.help}
      </FormControl.HelperText>
    </FormControl>
  );
});

export default ShadowPhone;
