import { API_URL, getUser } from "../../constants/Config";
import * as events from "../events";

export const fetchSignatures = (page = 1, limit = 50) => {
  return async (dispatch, getState) => {
    const { token } = getUser();
    // const { userTemplates } = getState().templates;

    const body = {
      skip: page === 1 ? 0 : (page - 1) * limit,
      limit,
    };

    // const ownerTemplates =
    //   userTemplates &&
    //   userTemplates.length > 0 &&
    //   userTemplates.filter((t) => t.createdUser._id === team);

    // if (ownerTemplates && ownerTemplates.length > 0) {
    //   const signatures = [];
    //   ownerTemplates.map((t) => {
    //     if (t.signature) {
    //       signatures.push(t.signature._id);
    //     }
    //     return t;
    //   });
    //   if (signatures.length > 0) {
    //     body["filter"] = { signatures };
    //   }
    // }

    try {
      const response = await fetch(`${API_URL}/signatures/get`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify(body),
      });

      if (!response.ok) {
        const errorRes = await response.json();
        let message = "Something went wrong!";
        if (errorRes && errorRes.error) {
          message = errorRes.error;
        }
        throw new Error(message);
      }

      const resData = await response.json();

      dispatch({
        type: events.SET_SIGNATURES,
        signatures: resData.data ? resData.data : [],
        total: resData.count ? resData.count : 0
      });
    } catch (err) {
      // send to custom analytics server
      console.log("fetchSignatures Error ", err);
      throw err;
    }
  };
};

export const deleteSignature = (_id) => {
  return async (dispatch, getState) => {
    const { token } = getUser();
    const response = await fetch(`${API_URL}/signatures/${_id}`, {
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body: JSON.stringify({ id: _id }),
    });
    if (!response.ok) {
      const errRes = await response.json();
      console.log(errRes);
      let message = "Something went wrong!";
      if (errRes && errRes.error) {
        message = errRes.error;
      }
      throw new Error(message);
    }
    dispatch({ type: events.DELETE_SIGNATURE, _id });
  };
};

export const saveSignature = (data) => {
  return async (dispatch, getState) => {
    try {
      const { token } = getUser();
      const { _id } = data;
      let url = `${API_URL}/signatures/add`;
      let method = "POST";
      if (_id) {
        url = `${API_URL}/signatures/${_id}`;
        delete data._id;
        method = "PATCH";
      }
      const response = await fetch(url, {
        method,
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify({ ...data }),
      });

      if (!response.ok) {
        const errorRes = await response.json();
        let message = "Something went wrong!";
        if (errorRes && errorRes.error) {
          message = errorRes.error;
        }
        throw new Error(message);
      }

      const resData = await response.json();

      if (resData.data) {
        dispatch({
          type: _id ? events.UPDATE_SIGNATURE : events.CREATE_SIGNATURE,
          data: resData.data,
        });
      }
    } catch (err) {
      console.log("create signature error", err);
      throw new Error(err.message);
    }
  };
};

export const testSMTP = (data) => {
  return async (dispatch, getState) => {
    try {
      const { token } = getUser();
      let url = `${API_URL}/smtp/test`;
      const response = await fetch(url, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify({ ...data }),
      });

      if (!response.ok) {
        const errorRes = await response.json();
        let message = "Something went wrong!";
        if (errorRes && errorRes.error) {
          message = errorRes.error;
        }
        throw new Error(message);
      }

      const resData = await response.json();
      return resData.data ? true : false;
    } catch (err) {
      console.log("test smtp error", err);
      throw new Error(err.message);
    }
  };
};

