export const uploadSignatureImages = (picture, setProgress, cb) => {
  return async (dispatch, getState) => {
    try {
      const {token, _id} = getUser();
      const formData = new FormData();
      formData.append("_id", _id);
      formData.append("default", true);
      formData.append("picture", {
        type: "image/jpg",
        uri: picture,
        name: "signature_" + _id + ".jpg",
      });
      var xhr = new XMLHttpRequest();
      xhr.open("POST", `${API_URL}/signatures/upload/images`, true);
      xhr.addEventListener(
        "progress",
        function (e) {
          var done = e.position || e.loaded,
            total = e.totalSize || e.total;
          dispatch({
            type: events.SET_PROGRESS,
            progress: Math.floor((done / total) * 1000) / 10,
          });
          console.log(
            "xhr progress: " + Math.floor((done / total) * 1000) / 10 + "%"
          );
        },
        false
      );
      if (xhr.upload) {
        xhr.upload.onprogress = function (e) {
          var done = e.position || e.loaded,
            total = e.totalSize || e.total;
          setProgress(Math.floor((done / total) * 1000) / 10);
          // console.log(
          //   'xhr.upload progress: ' +
          //     done +
          //     ' / ' +
          //     total +
          //     ' = ' +
          //     Math.floor((done / total) * 1000) / 10 +
          //     '%'
          // );
        };
      }
      xhr.setRequestHeader("Content-Type", "multipart/form-data");
      xhr.setRequestHeader("Authorization", `Bearer ${token}`);
      xhr.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 200) {
          // console.log(this.responseText);
          console.log("Signature Image uploaded successfully!");
          cb(JSON.parse(this.responseText));
        } else {
          console.log(this.responseText);
          // throw new Error("something went wrong");
        }
      };
      xhr.send(formData);
    } catch (err) {
      console.log("signature upload error", err);
      throw new Error("something went wrong");
    }
  };
};
