/**
 *
 * Add Signature Screen
 * Author:Justin Antony
 * last modified on 04/nov/2021
 *
 */
import React, { useState, useEffect, useRef } from "react";
import {
  Box,
  Pressable,
  Text,
  VStack,
  Center,
  Spinner,
  HStack,
  Hidden,
} from "native-base";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import _ from "lodash";

import { vw, vh } from "../../../util";
import * as signatureActions from "../../../store/actions/signatures";
import ShadowInput from "../../../components/ShadowInput";
import Colors from "../../../constants/Colors";
import { getUser } from "../../../constants/Config";
import ShadowUpload from "../../../components/ShadowUpload";
import ShadowModal from "../../../components/ShadowModal";
import ShadowSearch from "../../../components/ShadowSearch";
import ShadowPhone from "../../../components/ShadowPhone";
import ShadowButton from "../../../components/ShadowButton";
import EyeClosedIcon from "../../../components/svg/EyeClosedIcon";
import EyeOpenIcon from "../../../components/svg/EyeOpenIcon";

const AddSignature = ({ isOpen, setIsOpen, _showAlert }) => {
  const dispatch = useDispatch();
  const fRef = useRef([]);
  const currentSignature = useSelector(
    (state) => state.signatures.currentSignature
  );
  const editsmtp = false; // props.route.params?.editsmtp ?? false;
  const redirect = null; //props.route.params?.redirect ?? null;
  const [searchAddress, setSearchAddress] = useState(null);
  const [smtpTest, setSmtpTest] = useState(false);
  const [refresh, setRefresh] = useState(false);
  const [isLoading] = useState(false);
  const [isTesting, setIsTesting] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);
  const onClose = () => setIsOpen(false);
  const [isSmtpOpen, setIsSmtpOpen] = React.useState(false);
  const onSmtpClose = () => setIsSmtpOpen(false);
  const [isAlertOpen, setIsAlertOpen] = React.useState(false);
  const onAlertClose = () => setIsAlertOpen(false);
  const [alertConfig, setAlertConfig] = useState({
    header: "",
    desc: "",
    nolabel: "No",
    yeslabel: "Yes",
    yes: () => {},
    no: onAlertClose,
  });

  const {
    control,
    handleSubmit,
    formState: { errors },
    getValues,
    setValue,
    trigger,
    clearErrors,
  } = useForm({
    mode: "onChange",
    defaultValues: _.isEmpty(currentSignature)
      ? {
          label: "",
          // signoff: '',
          name: "",
          image: "0",
          company_logo: "0",
          phone: "",
          email: "",
          website: "",
          address1: "",
          address2: "",
          city: "",
          state: "",
          zipcode: "",
          disclosure: "",
          host: "",
          port: "",
          username: "",
          password: "",
        }
      : {
          label: currentSignature.label,
          //signoff: currentSignature.signoff,
          name: currentSignature.name,
          image: currentSignature.image,
          // company_logo: currentSignature.image,
          phone: currentSignature.phone,
          email: currentSignature.email,
          website: currentSignature.website,
          address1: currentSignature.address1,
          address2: currentSignature.address2,
          city: currentSignature.city,
          state: currentSignature.state,
          zipcode: currentSignature.zipcode,
          disclosure: currentSignature.disclosure,
          host: currentSignature.smtp ? currentSignature.smtp.host : "",
          port: currentSignature.smtp ? currentSignature.smtp.port : "",
          username: currentSignature.smtp ? currentSignature.smtp.username : "",
          password: currentSignature.smtp ? currentSignature.smtp.password : "",
        },
  });

  useEffect(() => {
    if (isSaving) {
      handleSubmit(checkSmtp, () => {
        _showAlert("error", "Validation failed. Check your entries.");
        setIsSaving(false);
      })();
    }
  }, [isSaving, _showAlert, handleSubmit]);

  useEffect(() => {
    if (refresh && searchAddress && searchAddress["shortaddress"]) {
      const shortaddress = searchAddress["shortaddress"];
      setValue("address1", shortaddress["address1"]);
      trigger("address1");
      setValue("address2", shortaddress["address2"]);
      trigger("address2");
      setValue("city", shortaddress["city"]);
      trigger("city");
      setValue("state", shortaddress["state"]);
      trigger("state");
      setValue("zipcode", shortaddress["postcode"]);
      trigger("postcode");
      setRefresh(false);
    }
  }, [searchAddress, refresh]);

  const checkSmtp = async (data) => {
    if (smtpTest) {
      const buttons = [];
      const config = {
        header: "Attention!",
        desc: "Test your SMTP using the Test SMTP button before saving your signature",
        nolabel: "Test Now",
        yeslabel: "",
        yes: () => {},
        no: async () => {
          await testSmtp(data);
          onClose();
        },
      };
      if (currentSignature && currentSignature._id) {
        if (!currentSignature.templates) {
          // buttons.push({
          //   text: "Clear SMTP & Save",
          //   onPress: async () => {
          //     saveHandler({ ...data, clearSmtp: true });
          //   },
          // });
          config.yeslabel = "Clear SMTP & Save";
          config.yes = saveHandler({ ...data, clearSmtp: true });
        } else {
          const foundEmail = currentSignature.templates.find(
            (t) => t.templatetype === "email"
          );
          if (!foundEmail) {
            // buttons.push({
            //   text: "Clear SMTP & Save",
            //   onPress: async () => {
            //     saveHandler({ ...data, clearSmtp: true });
            //   },
            // });
            config.yeslabel = "Clear SMTP & Save";
            config.yes = saveHandler({ ...data, clearSmtp: true });
          }
        }
      } else {
        // buttons.push({
        //   text: "Clear SMTP & Save",
        //   onPress: async () => {
        //     saveHandler({ ...data, clearSmtp: true });
        //   },
        // });
        config.yeslabel = "Clear SMTP & Save";
        config.yes = saveHandler({ ...data, clearSmtp: true });
      }
      // buttons.push({
      //   text: "Test Now",
      //   onPress: async () => {
      //     await testSmtp(data);
      //     return;
      //   },
      // });

      setAlertConfig(config);
      setIsOpen(true);

      // Alert.alert(
      //   "Attention!",
      //   "Test your SMTP using the Test SMTP button before saving your signature",
      //   buttons
      // );
      return;
    } else {
      saveHandler({ ...data, clearSmtp: false });
    }
  };

  const saveHandler = async (data) => {
    if (Object.keys(errors).length > 0) {
      _showAlert("error", "Validation failed, check your entries.");
      return;
    }

    setIsSaving(true);

    const postdata = {
      label: data.label,
      signoff: "Truly,",
      name: data.name,
      image: data.image || "0",
      company_logo: data.company_logo || "0",
      phone: data.phone,
      email: data.email,
      website: data.website,
      address1: data.address1,
      address2: data.address2,
      city: data.city,
      state: data.state === "" ? "Alabama" : data.state,
      zipcode: data.zipcode.toString(),
      disclosure: data.disclosure,
      clearSmtp: data.clearSmtp,
    };

    if (data.host && data.host !== "") {
      postdata["smtp"] = {
        host: data.host,
        port: data.port,
        username: data.username,
        password: data.password,
      };
    }

    if (currentSignature && currentSignature._id) {
      postdata["_id"] = currentSignature._id;
    }

    const action = signatureActions.saveSignature(postdata);

    try {
      const msg = !postdata._id
        ? "Signature created successfully."
        : "Signature updated successfully.";
      await dispatch(action);
      _showAlert("success", msg);
      // console.log("redirect", redirect);
      if (editsmtp || redirect === "home") {
        setIsOpen(false);
      } else {
        setIsOpen(false);
      }
    } catch (err) {
      console.log("Created Error", err);
      _showAlert("error", err.message);
      setIsSaving(false);
    }
  };

  const testSmtp = async (saveData = null) => {
    // if (Object.keys(errors).length > 0) {
    //   _showAlert("error", "Validation Failed. Check your entries");
    //   return;
    // }
    const user = getUser();

    if (
      getValues &&
      getValues("host") !== "" &&
      getValues &&
      getValues("port") !== "" &&
      getValues &&
      getValues("username") !== "" &&
      getValues &&
      getValues("password") !== ""
    ) {
      try {
        setIsTesting(true);

        const data = {
          smtphost: getValues("host"),
          smtpport: getValues("port"),
          smtpuser: getValues("username"),
          smtppwd: getValues("password"),
          from: user.name,
          to: user.email,
        };

        const action = signatureActions.testSMTP(data);
        const sent = await dispatch(action);
        const msg = "SMTP is configured successfully.";
        if (sent) {
          _showAlert("success", msg);
          setSmtpTest(false);
        }
        setIsTesting(false);
        if (saveData) {
          saveHandler({
            ...saveData,
            host: data.smtphost,
            port: data.smtpport,
            username: data.smtpuser,
            password: data.smtppwd,
            clearSmtp: false,
          });
        }
      } catch (err) {
        console.log("Created Error", err);
        _showAlert("error", "SMTP configuration failed, please try again.");
        setIsTesting(false);
        setIsSaving(false);
      }
    } else {
      // _showAlert("error", "Validation Failed. Check your entries");
      trigger("host", { shouldFocus: true });
      trigger("port");
      trigger("username");
      trigger("password");
    }
  };

  const deleteHandler = async () => {
    if (currentSignature._id) {
      setIsDeleting(true);
      try {
        const action = signatureActions.deleteSignature(currentSignature._id);
        await dispatch(action);
        _showAlert("success", "Signature deleted successfully.");
        setIsAlertOpen(false);
        setIsOpen(false);
      } catch (err) {
        _showAlert("error", err.message);
        setIsDeleting(false);
      }
    }
  };

  // console.log("errors", errors, getValues());

  return isLoading ? (
    <Center bg={"white"} flex={1}>
      <Spinner size={"small"} color={Colors.primary} />
    </Center>
  ) : (
    <>
      <VStack bg={"white"} width={"100%"} mb={20}>
        <ShadowModal
          size='xl'
          initialFocusRef={fRef.current["label"]}
          // height={
          //   sz === "base" || sz === "sm"
          //     ? window.screen.height - 20
          //     : window.screen.height - 100
          // }
          // BodyHeight={vh(500)}
          header={
            currentSignature && currentSignature._id
              ? "Edit Signature"
              : "Create Signature"
          }
          isOpen={isOpen}
          close={() => {
            onClose();
          }}
          cancel={() => {
            onClose();
          }}
          isLoading={isSaving}
          save={() => {
            if (Object.keys(errors).length === 0) {
              setIsSaving(true);
            }
          }}
          // width={sz === "base" || sz === "sm" ? "99vw" : vw(650)}
          buttons={
            <HStack space={2}>
              {currentSignature && currentSignature._id && (
                <ShadowButton
                  variant='outline'
                  label={"Delete"}
                  style={{
                    backgroundColor: "rgba(255,0,0,0.3)",
                  }}
                  onPress={() => {
                    if (
                      currentSignature &&
                      currentSignature.templates &&
                      currentSignature.templates.length > 0
                    ) {
                      setAlertConfig({
                        header: "Sorry!",
                        desc: "Cannot delete signature, found associated templates",
                        nolabel: "Okay",
                        yeslabel: "",
                        yes: () => {},
                        no: onAlertClose,
                      });
                      setIsAlertOpen(true);
                      return;
                    }

                    setAlertConfig({
                      header: "Delete!",
                      desc: "Are you sure you want to delete this signature?",
                      nolabel: "No",
                      yeslabel: "Yes",
                      yes: deleteHandler,
                      no: onAlertClose,
                    });
                    setIsAlertOpen(true);
                    return;
                  }}
                />
              )}
              <ShadowButton
                space={2}
                variant='outline'
                label={"Cancel"}
                onPress={() => {
                  onClose();
                }}
                style={{
                  height: vh(36),
                }}
              />
              <ShadowButton
                space={3}
                label={"Save"}
                isLoading={isSaving}
                onPress={() => {
                  clearErrors();
                  if (Object.keys(errors).length === 0) {
                    setIsSaving(true);
                  }
                }}
                style={{
                  height: vh(36),
                }}
              />
            </HStack>
          }
        >
          {/* form for small screens */}
          <Hidden from='md'>
            <VStack space={8} style={{ width: "94%" }}>
              <ShadowInput
                autoFocus={true}
                control={control}
                isRequired
                name='label'
                label='Label '
                placeholder='Signature #'
                invalid={errors.label ? true : false}
                error={errors.label ? errors.label.message : ""}
                rules={{
                  required: {
                    value: true,
                    message: "Please enter signature label.",
                  },
                }}
              />
              <ShadowInput
                control={control}
                isRequired
                name='name'
                label='Signature Name '
                placeholder='Enter signature name'
                invalid={errors.name ? true : false}
                error={errors.name ? errors.name.message : ""}
                rules={{
                  required: {
                    value: true,
                    message: "Please enter signature name.",
                  },
                }}
              />
              <ShadowPhone
                control={control}
                isRequired
                name='phone'
                label='Phone Number '
                placeholder='Phone'
                initialValue={
                  currentSignature && currentSignature.phone
                    ? currentSignature &&
                      currentSignature.phone.replace(/ /g, "").replace("+", "")
                    : ""
                }
                invalid={errors.phone ? true : false}
                error={errors.phone ? errors.phone.message : ""}
                rules={{
                  required: {
                    value: true,
                    message: "Please enter your phone number.",
                  },
                }}
              />
              <VStack>
                <Pressable
                  onPress={() => {
                    setIsSmtpOpen(true);
                  }}
                  style={{
                    position: "absolute",
                    right: 0,
                    top: vh(5),

                    zIndex: 999,
                  }}
                >
                  <Text
                    style={{
                      fontFamily: "Poppins_medium",
                      fontSize: vw(13),
                      color: Colors.primary,
                    }}
                  >
                    Configure SMTP
                  </Text>
                </Pressable>
                <ShadowInput
                  control={control}
                  isRequired
                  name='email'
                  label='Email Address '
                  placeholder='me@example.com'
                  invalid={errors.email ? true : false}
                  error={errors.email ? errors.email.message : ""}
                  rules={{
                    required: {
                      value: true,
                      message: "Please enter your email address.",
                    },

                    maxLength: {
                      value: 255,
                      message:
                        "Email address cannot be more than 255 characters.",
                    },
                  }}
                />
              </VStack>

              <ShadowInput
                control={control}
                name='website'
                label='Website URL '
                placeholder='Enter website url'
                invalid={errors.website ? true : false}
                error={errors.website ? errors.website.message : ""}
                rules={{}}
                style={{ zIndex: 1 }}
              />
              {/* <ShadowSearch
              setSearch={setSearch}
              style={{
                borderColor: "rgba(0,0,0,0.15)",
                borderWidth: vw(1),
                marginLeft: 0,
                width: "99%",
              }}
            /> */}
              <ShadowInput
                control={control}
                isRequired
                name='address1'
                label='Address Line 1 '
                placeholder='Enter address line 1'
                invalid={errors.address1 ? true : false}
                error={errors.address1 ? errors.address1.message : ""}
                rules={{
                  required: {
                    value: true,
                    message: "Please enter your address.",
                  },
                }}
                // value={address.address1}
              />
              <ShadowInput
                control={control}
                name='address2'
                label='Address Line 2 '
                placeholder='Enter address line 2'
                invalid={errors.address2 ? true : false}
                error={errors.address2 ? errors.address2.message : ""}
                rules={{}}
                // value={address.address2}
              />
              <ShadowInput
                control={control}
                isRequired
                name='city'
                label='City '
                placeholder='Enter city'
                invalid={errors.city ? true : false}
                error={errors.city ? errors.city.message : ""}
                rules={{
                  required: {
                    value: true,
                    message: "Please enter city.",
                  },
                }}
                // value={address.city}
              />
              <HStack
                flex={1}
                width={"96%"}
                space={3}
                justifyContent={"space-between"}
              >
                <ShadowInput
                  control={control}
                  isRequired
                  name='state'
                  label='State '
                  placeholder='Enter state'
                  invalid={errors.state ? true : false}
                  error={errors.state ? errors.state.message : ""}
                  rules={{
                    required: {
                      value: true,
                      message: "Please enter state.",
                    },
                  }}
                  style={{ width: "50%" }}
                />
                <ShadowInput
                  control={control}
                  isRequired
                  maxLength={5}
                  minLength={5}
                  name='zipcode'
                  label='Zipcode '
                  placeholder='Enter zipcode'
                  invalid={errors.zipcode ? true : false}
                  error={errors.zipcode ? errors.zipcode.message : ""}
                  rules={{
                    // valueAsNumber: true,
                    pattern: {
                      value: /^[0-9]{5}?$/,
                      message: "Please enter a valid zipcode",
                    },
                    required: { value: true, message: "Please enter zipcode" },
                    maxLength: {
                      value: 5,
                      message: "Please enter a valid zipcode",
                    },
                  }}
                  style={{ width: "50%" }}
                />
              </HStack>
              <HStack
                space={4}
                style={{
                  justifyContent: "space-between",
                  height: vh(60),
                }}
              >
                <Center>
                  <ShadowUpload
                    label='Upload Your Image '
                    path={
                      (currentSignature && currentSignature.image.toString()) ||
                      "0"
                    }
                    setPath={(value) => {
                      setValue("image", value);
                      trigger("image");
                    }}
                    invalid={errors.image ? true : false}
                    error={errors.image ? errors.image.message : ""}
                  />
                </Center>
                <Center>
                  <ShadowUpload
                    label='Upload Company Logo '
                    path={
                      (currentSignature && currentSignature.company_logo) || "0"
                    }
                    setPath={(value) => {
                      setValue("company_logo", value);
                      trigger("company_logo");
                    }}
                    invalid={errors.image ? true : false}
                    error={errors.image ? errors.image.message : ""}
                  />
                </Center>
              </HStack>
              <ShadowInput
                control={control}
                name='disclosure'
                label='Disclosure '
                placeholder='Enter disclosure'
                help="If you're a realtor, you may be required to disclose your MLS identification on your postcard. Feel free to include that here."
                invalid={errors.disclosure ? true : false}
                error={errors.disclosure ? errors.disclosure.message : ""}
                rules={{}}
              />
            </VStack>
          </Hidden>
          {/* form for big screens */}
          <Hidden only={["base", "sm"]}>
            <VStack
              space={2}
              style={{
                zoom: "95%",
                marginTop: vh(-20),
              }}
            >
              <HStack
                space={4}
                style={{
                  width: "100%",
                  justifyContent: "space-between",
                  // marginHorizontal: vw(20),
                  marginBottom: vh(10),
                }}
              >
                <ShadowInput
                  ref={(el) => (fRef.current["label"] = el)}
                  style={{ width: "48%" }}
                  autoFocus={true}
                  control={control}
                  isRequired
                  name='label'
                  label='Label '
                  onKeyPress={(e) => {
                    if (e.key === "Tab") {
                      e.preventDefault();
                      fRef.current["name"].focus();
                    }
                  }}
                  placeholder='Signature #'
                  invalid={errors.label ? true : false}
                  error={errors.label ? errors.label.message : ""}
                  rules={{
                    required: {
                      value: true,
                      message: "Please enter signature label.",
                    },
                  }}
                />
                <ShadowInput
                  ref={(el) => (fRef.current["name"] = el)}
                  onKeyPress={(e) => {
                    if (e.key === "Tab") {
                      e.preventDefault();
                      fRef.current["phone"].focus();
                    }
                  }}
                  style={{ width: "48%" }}
                  control={control}
                  isRequired
                  name='name'
                  label='Signature Name '
                  placeholder='Enter signature name'
                  invalid={errors.name ? true : false}
                  error={errors.name ? errors.name.message : ""}
                  rules={{
                    required: {
                      value: true,
                      message: "Please enter signature name.",
                    },
                  }}
                />
              </HStack>
              <HStack
                space={4}
                style={{
                  width: "100%",
                  justifyContent: "space-between",
                  // marginHorizontal: vw(20),
                  marginVertical: vh(10),
                  zIndex: 9999,
                }}
              >
                <ShadowPhone
                  ref={(el) => (fRef.current["phone"] = el)}
                  onKeyPress={(e) => {
                    console.log(e.code);
                    if (e.code === "Tab") {
                      e.preventDefault();
                      fRef.current["email"].focus();
                    }
                  }}
                  style={{ width: "48%" }}
                  control={control}
                  isRequired
                  name='phone'
                  label='Phone Number '
                  placeholder='Phone'
                  initialValue={
                    currentSignature && currentSignature.phone
                      ? currentSignature &&
                        currentSignature.phone
                          .replace(/ /g, "")
                          .replace("+", "")
                      : ""
                  }
                  invalid={errors.phone ? true : false}
                  error={errors.phone ? errors.phone.message : ""}
                  rules={{
                    required: {
                      value: true,
                      message: "Please enter your phone number.",
                    },
                  }}
                />
                <VStack style={{ width: "48%" }}>
                  <Pressable
                    onPress={() => {
                      setIsSmtpOpen(true);
                    }}
                    style={{
                      position: "absolute",
                      right: 0,
                      top: vh(5),

                      zIndex: 999,
                    }}
                  >
                    <Text
                      style={{
                        fontFamily: "Poppins_medium",
                        fontSize: vw(13),
                        color: Colors.primary,
                      }}
                    >
                      Configure SMTP
                    </Text>
                  </Pressable>
                  <ShadowInput
                    ref={(el) => (fRef.current["email"] = el)}
                    onKeyPress={(e) => {
                      if (e.key === "Tab") {
                        e.preventDefault();
                        fRef.current["website"].focus();
                      }
                    }}
                    control={control}
                    isRequired
                    name='email'
                    label='Email Address '
                    placeholder='me@example.com'
                    invalid={errors.email ? true : false}
                    error={errors.email ? errors.email.message : ""}
                    rules={{
                      required: {
                        value: true,
                        message: "Please enter your email address.",
                      },

                      maxLength: {
                        value: 255,
                        message:
                          "Email address cannot be more than 255 characters.",
                      },
                    }}
                  />
                </VStack>
              </HStack>
              <HStack
                style={{
                  width: "100%",
                  justifyContent: "space-between",
                  // marginHorizontal: vw(20),
                  marginVertical: vh(10),
                }}
              >
                <ShadowInput
                  ref={(el) => (fRef.current["website"] = el)}
                  onKeyPress={(e) => {
                    if (e.key === "Tab") {
                      e.preventDefault();
                      fRef.current["address1"].focus();
                    }
                  }}
                  control={control}
                  name='website'
                  label='Website URL '
                  placeholder='Enter website url'
                  invalid={errors.website ? true : false}
                  error={errors.website ? errors.website.message : ""}
                  rules={{}}
                  style={{ zIndex: 1 }}
                />
              </HStack>
              <VStack space={2} style={{ width: "100%" }}>
                <ShadowSearch
                  setAddress={setSearchAddress}
                  setRefresh={setRefresh}
                />
                <HStack
                  space={4}
                  style={{
                    width: "100%",
                    justifyContent: "space-between",
                    // marginHorizontal: vw(20),
                    marginVertical: vh(10),
                  }}
                >
                  <ShadowInput
                    ref={(el) => (fRef.current["address1"] = el)}
                    onKeyPress={(e) => {
                      if (e.key === "Tab") {
                        e.preventDefault();
                        fRef.current["address2"].focus();
                      }
                    }}
                    style={{ width: "48%" }}
                    control={control}
                    isRequired
                    name='address1'
                    label='Address Line 1 '
                    placeholder='Enter address line 1'
                    invalid={errors.address1 ? true : false}
                    error={errors.address1 ? errors.address1.message : ""}
                    rules={{
                      required: {
                        value: true,
                        message: "Please enter your address.",
                      },
                    }}
                    // value={address.address1}
                  />
                  <ShadowInput
                    ref={(el) => (fRef.current["address2"] = el)}
                    onKeyPress={(e) => {
                      if (e.key === "Tab") {
                        e.preventDefault();
                        fRef.current["city"].focus();
                      }
                    }}
                    style={{ width: "48%" }}
                    control={control}
                    name='address2'
                    label='Address Line 2 '
                    placeholder='Enter address line 2'
                    invalid={errors.address2 ? true : false}
                    error={errors.address2 ? errors.address2.message : ""}
                    rules={{}}
                    // value={address.address2}
                  />
                </HStack>
                <HStack
                  space={4}
                  style={{
                    width: "100%",
                    justifyContent: "space-between",
                    // marginHorizontal: vw(20),
                    marginVertical: vh(10),
                  }}
                >
                  <ShadowInput
                    ref={(el) => (fRef.current["city"] = el)}
                    onKeyPress={(e) => {
                      if (e.key === "Tab") {
                        e.preventDefault();
                        fRef.current["state"].focus();
                      }
                    }}
                    style={{ width: "48%" }}
                    control={control}
                    isRequired
                    name='city'
                    label='City '
                    placeholder='Enter city'
                    invalid={errors.city ? true : false}
                    error={errors.city ? errors.city.message : ""}
                    rules={{
                      required: {
                        value: true,
                        message: "Please enter city.",
                      },
                    }}
                    // value={address.city}
                  />
                  <HStack space={2} style={{ width: "48%" }}>
                    <ShadowInput
                      ref={(el) => (fRef.current["state"] = el)}
                      onKeyPress={(e) => {
                        if (e.key === "Tab") {
                          e.preventDefault();
                          fRef.current["zipcode"].focus();
                        }
                      }}
                      style={{ width: "48.5%" }}
                      control={control}
                      isRequired
                      name='state'
                      label='State '
                      placeholder='Enter state'
                      invalid={errors.state ? true : false}
                      error={errors.state ? errors.state.message : ""}
                      rules={{
                        required: {
                          value: true,
                          message: "Please enter state.",
                        },
                      }}
                      // value={address.state}
                    />
                    <ShadowInput
                      ref={(el) => (fRef.current["zipcode"] = el)}
                      onKeyPress={(e) => {
                        if (e.key === "Tab") {
                          e.preventDefault();
                          fRef.current["disclosure"].focus();
                        }
                      }}
                      style={{ width: "48.5%" }}
                      control={control}
                      isRequired
                      maxLength={5}
                      minLength={5}
                      name='zipcode'
                      label='Zipcode '
                      placeholder='Enter zipcode'
                      invalid={errors.zipcode ? true : false}
                      error={errors.zipcode ? errors.zipcode.message : ""}
                      rules={{
                        // valueAsNumber: true,
                        pattern: {
                          value: /^[0-9]{5}?$/,
                          message: "Invalid zipcode",
                        },
                        required: {
                          value: true,
                          message: "Please enter zipcode",
                        },
                        maxLength: {
                          value: 5,
                          message: "Invalid zipcode",
                        },
                        minLength: {
                          value: 5,
                          message: "Invalid zipcode",
                        },
                      }}
                    />
                  </HStack>
                </HStack>
              </VStack>
              <HStack
                space={4}
                style={{
                  width: "100%",
                  justifyContent: "space-between",
                  // marginHorizontal: vw(20),
                  marginVertical: vh(10),
                  height: vh(60),
                  zIndex: -1,
                }}
              >
                <Box style={{ width: "48%" }}>
                  <ShadowUpload
                    label='Click to Upload Your Image '
                    path={
                      (currentSignature && currentSignature.image.toString()) ||
                      "0"
                    }
                    setPath={(value) => {
                      setValue("image", value);
                      trigger("image");
                    }}
                    invalid={errors.image ? true : false}
                    error={errors.image ? errors.image.message : ""}
                  />
                </Box>
                <Box style={{ width: "48%" }}>
                  <ShadowUpload
                    label='Click to Upload Company Logo '
                    path={
                      (currentSignature && currentSignature.company_logo) || "0"
                    }
                    setPath={(value) => {
                      setValue("company_logo", value);
                      trigger("company_logo");
                    }}
                    invalid={errors.image ? true : false}
                    error={errors.image ? errors.image.message : ""}
                  />
                </Box>
              </HStack>
              <HStack
                style={{
                  width: "100%",
                  justifyContent: "space-between",
                  // marginHorizontal: vw(20),
                  marginVertical: vh(20),
                  marginTop: vh(30),
                }}
              >
                <ShadowInput
                  ref={(el) => (fRef.current["disclosure"] = el)}
                  control={control}
                  name='disclosure'
                  label='Disclosure '
                  placeholder='Enter disclosure'
                  help="If you're a realtor, you may be required to disclose your MLS identification on your postcard. Feel free to include that here."
                  invalid={errors.disclosure ? true : false}
                  error={errors.disclosure ? errors.disclosure.message : ""}
                  rules={{}}
                />
              </HStack>
            </VStack>
          </Hidden>
        </ShadowModal>

        {/* Configure SMTP */}
        <ShadowModal
          initialFocusRef={fRef.current["host"]}
          size={"lg"}
          header='Configure SMTP'
          isOpen={isSmtpOpen}
          close={() => {
            onSmtpClose();
          }}
          buttons={
            <ShadowButton
              space={2}
              label={"Test SMTP"}
              isDisabled={!smtpTest && !isTesting}
              isLoading={isTesting}
              onPress={() => {
                testSmtp(null);
              }}
              style={{
                height: vh(36),
              }}
            />
          }
        >
          <VStack
            style={{ zoom: "90%", marginTop: vh(-20) }}
            justifyContent={"center"}
          >
            <Text>
              This SMTP will be used to send email campaigns from the given
              email address
            </Text>
            <HStack
              space={4}
              style={{
                width: "100%",
                justifyContent: "space-between",
                // marginHorizontal: vw(20),
                marginVertical: vh(20),
              }}
            >
              <ShadowInput
                style={{ width: "48%" }}
                ref={(el) => (fRef.current["host"] = el)}
                onKeyPress={(e) => {
                  if (e.key === "Tab") {
                    e.preventDefault();
                    fRef.current["port"].focus();
                  }
                }}
                autoFocus={true}
                control={control}
                name='host'
                label='Host '
                placeholder='smtp.gmail.com'
                onChangeText={(v) => {
                  setSmtpTest(true);
                  // setValue("host", v);
                  // trigger("host");
                }}
                invalid={errors.host ? true : false}
                error={errors.host ? errors.host.message : ""}
                rules={{
                  required: {
                    value: isSmtpOpen,
                    message: "Please enter host.",
                  },

                  maxLength: {
                    value: 255,
                    message: "Host cannot be more than 255 characters.",
                  },
                }}
              />
              <ShadowInput
                ref={(el) => (fRef.current["port"] = el)}
                onKeyPress={(e) => {
                  if (e.key === "Tab") {
                    e.preventDefault();
                    fRef.current["username"].focus();
                  }
                }}
                style={{ width: "48%" }}
                control={control}
                name='port'
                label='Port '
                placeholder='25/587/465'
                onChangeText={(v) => {
                  setSmtpTest(true);
                  // setValue("port", v);
                  // trigger("port");
                }}
                invalid={errors.port ? true : false}
                error={errors.port ? errors.port.message : ""}
                rules={{
                  required: {
                    value: isSmtpOpen,
                    message: "Please enter port.",
                  },

                  maxLength: {
                    value: 5,
                    message: "Port cannot be more than 5 characters.",
                  },
                }}
              />
            </HStack>
            <HStack
              space={4}
              style={{
                width: "100%",
                justifyContent: "space-betwee0n",
                // marginHorizontal: vw(20),
                marginVertical: vh(20),
              }}
            >
              <ShadowInput
                ref={(el) => (fRef.current["username"] = el)}
                onKeyPress={(e) => {
                  if (e.key === "Tab") {
                    e.preventDefault();
                    fRef.current["password"].focus();
                  }
                }}
                style={{ width: "48%" }}
                control={control}
                name='username'
                label='Username '
                placeholder='smtp username'
                onChangeText={(v) => {
                  setSmtpTest(true);
                  // setValue("username", v);
                  // trigger("username");
                }}
                invalid={errors.username ? true : false}
                error={errors.username ? errors.username.message : ""}
                rules={{
                  required: {
                    value: isSmtpOpen,
                    message: "Please enter username.",
                  },
                }}
              />
              <ShadowInput
                ref={(el) => (fRef.current["password"] = el)}
                style={{ width: "48%" }}
                type='password'
                control={control}
                name='password'
                label='Password '
                placeholder='password'
                onChangeText={(v) => {
                  setSmtpTest(true);
                  // setValue("password", v);
                  // trigger("password");
                }}
                rightIcon={
                  <Pressable
                    style={{ marginHorizontal: 5 }}
                    onPress={() => {
                      fRef.current["password"].type =
                        fRef.current["password"].type === "text"
                          ? "password"
                          : "text";
                      setShowPassword(!showPassword);
                    }}
                  >
                    {showPassword ? <EyeClosedIcon /> : <EyeOpenIcon width={20} height={20} />}
                  </Pressable>
                }
                invalid={errors.password ? true : false}
                error={errors.password ? errors.password.message : ""}
                rules={{
                  required: {
                    value: isSmtpOpen,
                    message: "Please enter password.",
                  },
                }}
              />
            </HStack>
          </VStack>
        </ShadowModal>
      </VStack>
      <ShadowModal
        size={"md"}
        isOpen={isAlertOpen}
        close={() => {
          setIsAlertOpen(false);
        }}
        header={alertConfig.header}
        buttons={
          <HStack space={2}>
            {alertConfig.yeslabel === "" ? (
              <ShadowButton
                space={4}
                onPress={() => {
                  setIsAlertOpen(false);
                }}
                color='rgba(0,0,0, 0.5)'
                variant='outline'
                label='Okay'
                style={{
                  height: vh(32),
                }}
              />
            ) : (
              <HStack space={2}>
                <ShadowButton
                  space={4}
                  onPress={() => {
                    setIsAlertOpen(false);
                  }}
                  color='rgba(0,0,0, 0.5)'
                  variant='outline'
                  label='No'
                  style={{
                    height: vh(32),
                  }}
                />
                <ShadowButton
                  space={4}
                  isLoading={isDeleting}
                  onPress={alertConfig.yes}
                  color='white'
                  label='Yes'
                  style={{
                    height: vh(32),
                  }}
                />
              </HStack>
            )}
          </HStack>
        }
      >
        <Text
          style={{
            fontFamily: "Poppins_medium",
            fontSize: vw(15),
            color: "rgba(0,0,0,0.8)",
          }}
        >
          {alertConfig.desc}
        </Text>
      </ShadowModal>
    </>
  );
};

export default AddSignature;
